.PainelComposicao-painel {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    row-gap: 10px;
    width: 70%;
    padding: 10px;
    border-radius: 8px;
    background-color: #FFF;
}

.PainelComposicao-painel:hover {
    border: 1px solid #4CBC73;
}

.PainelComposicao-titulo {
    font-size: 14px;
    font-weight: bold;
}

.PainelComposicao-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    width: 100%;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
}

.PainelComposicao-container::-webkit-scrollbar {
    height: 8px;
    background-color: #E0E6EA;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

.PainelComposicao-container::-webkit-scrollbar-thumb {
    background-color: #007AD9;
    border-radius: 20px;
    width: 10px;
}

.Categoria-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 80%;
    width: 200px;
    padding: 10px;
    border-radius: 8px;
    box-shadow:  6px 6px 9px #b3b3b3,
             -6px -6px 9px #ffffff;
}

.Categoria-titulo {
    font-size: 12px;
    font-weight: bold;
    margin-bottom: 10px;
}

.Subprocesso-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 90%;
}

.Subprocesso-texto {
    font-size: 10px;
}

.Texto-lista-vazia {
    font-size: 14px;
    width: 100%;
    height: 100%;
    text-align: center;
}