.App {
  background-color: #F1F2F2;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  row-gap: 15px;
}

.App-input-username {
  background-color: #FFF;
  color: #474747;
  width: 24%;
  height: 30px;
  padding-left: 10px;
  border-width: 0;
  border-radius: 5px;
}

.App-input-username::placeholder {
  color: #DDD;
}

.App-input-username:focus {
  border-width: 1px;
  border-style: solid;
  outline: 0px;
  border-color: #4CBC73;
}

.App-input-password {
  background-color: #FFF;
  color: #474747;
  width: 24%;
  height: 30px;
  padding-left: 10px;
  border-width: 0;
  border-radius: 5px;
}

.App-input-password::placeholder {
  color: #DDD;
}

.App-input-password:focus {
  border-width: 1px;
  border-style: solid;
  outline: 0px;
  border-color: #4CBC73;
}

.App-button-entrar {
  background-color: #4DBCE4;
  border-width: 0;
  border-radius: 5px;
  width: 25%;
  height: 30px;
  color: #FFF;
}

.App-button-entrar:active {
  background-color: #4DBCE440;
}

.App-manterConectado {
  color: #474747;
  font-size: 8;
}

.App-esqueceuSenha {
  color: #474747;
  font-size: 8;
  font-weight: bold;
}

.App-button-inscrever {
  background-color: #FFCD39;
  border-width: 0;
  border-radius: 5px;
  width: 25%;
  height: 30px;
  color: #FFF;
}

.App-button-inscrever:active {
  background-color: #FFCD3940;
}

.App-link {
  color: #61dafb;
}

.Header {
  background-color: #4CBC73;
  width: 100%;
  height: 50px;
}