.Grafico-grupo-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 50%;
    height: auto;
    padding: 10px;
    row-gap: 10px;
    border-radius: 8px;
    background-color: #FFF;
}

.Grafico-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 95%;
    height: 210px;
    padding: 20px;
    row-gap: 10px;
    border-radius: 8px;
    background-color: #F1F2F2;
}

.Grafico-titulo {
    font-size: 14px;
    font-weight: bold;
}

.Grafico-info-container {
    display: flex;
    width: 100%;
}

.Grafico-info-container-interno {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    row-gap: 5px;
}

.Grafico-linha-info {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.Grafico-linha-realizado-col1 {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    width: 15%;
    gap: 5px;
}

.Grafico-linha-realizado-col2 {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    width: 30%;
    font-size: 14px;
    gap: 10px;
}

.Grafico-info-media {
    display: flex;
    flex-direction: column;
}

.Grafico-info-media-texto {
    font-size: 10px;
}

.Grafico-info-media-divisor {
    display: flex;
    height: 0.5px;
    width: 100%;
    background-color: #474747;
}

.Grafico-linha-realizado-col3 {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    width: 30%;
    font-size: 14px;
}

.Grafico-linha-realizado-col4 {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    width: 30%;
    font-size: 14px;
}