.PainelOcorrencias-painel {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 10px;
    width: 30%;
    padding: 10px;
    border-radius: 8px;
    background-color: #FFF;
}

.PainelOcorrencias-painel:hover {
    border: 1px solid #4CBC73;
}

.PainelOcorrencias-titulo {
    font-size: 14px;
    font-weight: bold;
}

.PainelOcorrencias-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border-radius: 8px;
    gap: 8px;
    width: 90%;
    height: 80px;
    padding-inline: 8px;
    background-color: #FFF;
}

.PainelOcorrencias-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 30%;
    height: 90%;
    row-gap: 10px;
    border-radius: 8px;
    border: 1px solid #47474740;
    background-color: #EEE;
}

.PainelOcorrencias-item-titulo {
    font-size: 14px;
    font-weight: bold;
    color: #474747;
}

.PainelOcorrencias-item-conteudo {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 20px;
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
}

.PainelOcorrencias-item-conteudo-valor {
    font-size: 22px;
    font-weight: bold;
    color: #474747;
}

.PainelOcorrencias-botao {
    border-width: 0px;
    border-radius: 8px;
    width: 80%;
    height: 25px;
    background-color: #FFCD39;
    color: #FFF;
}

.PainelOcorrencias-botao:active {
    background-color: #FFCD3940;
}