.Timeline-painel {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 10px;
    width: 95%;
    padding: 10px;
    border-radius: 8px;
    background-color: #FFF;
}

.Timeline-painel:hover {
    border: 1px solid #4CBC73;
}

.Timeline-titulo {
    font-size: 14px;
    font-weight: bold;
}

.Timeline-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 8px;
    gap: 8px;
    width: 99%;
    height: 150px;
    padding-inline: 8px;
    background-color: #FFF;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
}

.Timeline-container::-webkit-scrollbar {
    height: 8px;
    background-color: #E0E6EA;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

.Timeline-container::-webkit-scrollbar-thumb {
    background-color: #007AD9;
    border-radius: 20px;
    width: 10px;
}

.Timeline-container-item-mes {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    padding: 8px;
    background-color: #007AD9;
    height: 75%;
    width: 100px;
    border-radius: 8px;
    border-radius: 8px;
    box-shadow:  6px 6px 9px #b3b3b3,
             -6px -6px 9px #ffffff;
}

.Timeline-texto-item-mes {
    color: #FFF;
    font-size: 12px;
    font-weight: bold;
    transform: rotate(-90deg);
    user-select: none;
}

.Timeline-icone-item-mes {
    color: #FFF;
    font-weight: bold;
    user-select: none;
}

.Timeline-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    row-gap: 3px;
    justify-content: center;
    min-width: 200px;
    max-width: 220px;
    height: 75%;
    max-height: 75%;
    background-color: #FFF;
    border-radius: 8px;
    box-shadow:  6px 6px 9px #b3b3b3,
             -6px -6px 9px #ffffff;
}

.Timeline-item-data {
    font-size: 16px;
    font-weight: bold;
    user-select: none;
}

.Timeline-item-subprocesso {
    font-weight: bold;
    font-size: 11px;
    user-select: none;
}

.Timeline-item-residuo-quantidade {
    font-size: 11px;
    user-select: none;
}

.Timeline-item-observacao {
    width: 100%;
    font-size: 10px;
    text-align: left;
    padding-inline: 10px;
    white-space: wrap;
    user-select: none;
}

.Timeline-item-divisor {
    width: 90%;
    height: 0.5px;
    background-color: black;
}

.Lista-vazia-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: #FFF;
}

.Lista-vazia-texto {
    font-size: 14px;
    color: #474747;
}