.Home-container-componentes {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding-block: 20px;
    row-gap: 20px;
    background-color: #F1F2F2;
}

.Home-painel-unidades {
    display: flex;   
    flex-direction: row; 
    width: 96.5%;
    align-items: center;
    justify-content:flex-start;
    gap: 8px;
}

.Home-painel-ocorrencias {
    display: flex;
    flex-direction: row;
    width: 96.5%;
    gap: 20px;
}

.Home-painel-indicadores {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 95%;
    padding: 10px;
    row-gap: 10px;
    background-color: #FFF;
    border-radius: 8px;
}

.Home-painel-indicadores:hover {
    border: 1px solid #4CBC73;
}

.Home-painel-indicadores-titulo {
    font-size: 14px;
    font-weight: bold;
}

.Home-container-graficos {
    display: flex;
    flex-direction: row;
    width: 95%;
    align-items: center;
    justify-content:space-between;
    padding: 10px;
    gap: 20px;
    border-radius: 8px;
    background-color: #FFF;
}

.Home-container-botoes-graficos {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 95%;
}

.Home-container-botao-individual {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 49.2%;
}

.Grafico-botao {
    width: 60%;
    height: 25px;
    border-width: 0px;
    border-radius: 8px;
    background-color: #FFCD39;
    color: #FFF;
}

.Grafico-botao:active {
    background-color: #FFCD3940;
}

.Texto-lista-vazia {
    font-size: 14px;
}

.Home-grupo-userInfo {
    display: flex;   
    flex-direction: row; 
    align-items: flex-end;
    width: 30%;
    height: 50px;
    justify-content: center;
    gap: 8px;
}

.Home-texto-userInfo {
    height: 30px;
    width: 100%;
    border-width: 0px;
    border-radius: 5px;
    color: #474747;
    text-align: left;
    background-color: #FFF;
}

.Home-grupo-select-item {
    display: flex;
    flex-direction: column;
    align-items: left;
    width: 20%;
}

.Home-grupo-botoes {
    display: flex;   
    flex-direction: row; 
    align-items: flex-end;
    width: 30%;
    height: 50px;
    justify-content: center;
    gap: 8px;
}

.Home-botoes {
    height: 30px;
    width: 100%;
    border-width: 0px;
    border-radius: 5px;
    color: #FFF;
    background-color: #4DBCE4;
}

.Home-botoes:active {
    background-color: #4DBCE440;
}

.Home-lista-label {
    width: 100%;
    font-size: 14px;
}

.Home-lista {
    width: 100%;
    height: 30px;
    border-width: 0px;
    border-radius: 5px;
    background-color: #FFF;
    color: #474747;
}

.Home-lista:focus {
    border-width: 1px;
    border-style: solid;
    outline: 0px;
    border-color: #4CBC73;
}
